import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
import { Device } from '@ionic-native/device';
@IonicPage()
@Component({
  selector: 'page-reviewmain',
  templateUrl: 'reviewmain.html',
})
export class ReviewmainPage {

  User:any={
		Role:'',
		avatar: '',
		created_at: '',
		dob: '',
		email: '',
		first_name: '',
		id: 0,
		last_login: '',
		last_name: '',
		password: '',
		permissions: '',
		phone: '',
		status: 0,
		updated_at: '',
		username: ''
	};
	DeviceID:string="";

  constructor(public navCtrl: NavController, public navParams: NavParams, public httpClient:HttpClient, public device:Device, public toastCtrl: ToastController) {
	  this.LoadProfile();
  }
	LoadProfile()
	{
		this.DeviceID=this.device.uuid;
		if(this.DeviceID==null)
		{
			this.DeviceID="534b8b5aeb906015";
		}
		this.httpClient.post<any>('http://uber.ptezone.com.au/api/CheckLogin',{
			DeviceID:this.DeviceID
		})
		.subscribe(data => {
			console.log(data);
			this.User=data.User;
		},
		err => {
			
		})
	}
	GiveReviews()
	{
		this.navCtrl.push("GivereviewPage");
	}
	ViewReviews()
	{
		this.navCtrl.push("MyreviewPage");
	}
  ionViewDidLoad() {
    console.log('ionViewDidLoad ReviewmainPage');
  }

}



